import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { EditForm, EditViewProps, ERROR, getSelectedItem, InputGroupInline, InputType, showToast, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoEvento } from '../../../../models/dtos/mfp/grupoEvento';
import { criarEventosPadroes } from '../../../../services/mfp.service';

interface GrupoEventoEditViewProps extends EditViewProps<GrupoEvento> {}

const GrupoEventoEditView: FC<GrupoEventoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<GrupoEvento>({ defaultValues: selectedItem, mode: 'onBlur' });
  const [isInsert, setIsInsert] = useState(false);

  useEffect(
    () => {
      setIsInsert(!formProps.getValues().id);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const afterPersist = (grupo: GrupoEvento) => {
    if (isInsert) {
      criarEventosPadroes(grupo, {
        errorFunction: mensagem => {
          showToast(mensagem, ERROR);
        },
        thenFunction: result => {
          return result;
        },
      });
    }
  };

  return (
    <WindowComponent title="Manutenção de Grupo de Eventos">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} afterPersist={afterPersist}>
        <InputGroupInline
          formProps={{ ...formProps, validation: { maxLength: 9 } }}
          id="codigo"
          isPrependLabel={true}
          label="Código"
          placeholder="Informe o Código"
          tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
          type={InputType.NUMBER}
        />
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
          id="descricao"
          isPrependLabel={true}
          label="Descrição"
          placeholder="Informe a Descrição."
        />
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoEventoEditView;
